import React, { useState, useEffect } from 'react'
import modifyNums from '../services/util'
import { Jumbotron } from 'react-bootstrap'


const MultiplicadorConstante = () => {
    let [d, setD] = useState(0);
    let [seed, setSeed] = useState(0);
    let [seed2, setSeed2] = useState(0);
    let [amount, setAmount] = useState(0);
    let [generatedNums, setGeneratedNums] = useState([]);
    useEffect(() => {
        let seedLength = seed.toString().length;
        let seed2Length = seed2.toString().length
        setD(seedLength > seed2Length ? seedLength : seed2Length);
    }, [seed, seed2])
    const constuct = (v, w, x, y, z) => {
        return {
            v,
            w,
            x,
            y,
            z
        };
    };
    const generateNums = () => {
        if (seed.length !== seed2.length) {
            alert('La semilla y la constante tienen que tener los mismos digitos');
            return;
        }
        let tempArr = [];
        let seedSquare = seed * seed2;
        let middleSeedValues = modifyNums.getCentro(seedSquare, d);
        tempArr.push(constuct(seed2, seed, seedSquare, middleSeedValues, modifyNums.transformToR(middleSeedValues, d)));
        for (let i = 1; i < amount; i++) {
            seedSquare = seed2 * tempArr[i - 1].y;
            middleSeedValues = modifyNums.getCentro(seedSquare, d);
            tempArr.push(constuct(seed2, tempArr[i - 1].y, seedSquare, middleSeedValues, modifyNums.transformToR(middleSeedValues, d)));
        }
        setGeneratedNums(tempArr);
    }

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Algoritmo Multiplicador Constante
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div>
                <div className='row'>
                    <div className='col-2' >
                        <label htmlFor='semilla'>Semilla:</label>
                        <input id='semilla' type='number' className="form-control" value={seed} min='100' max='999999' onChange={(e) => setSeed(e.target.value)} />
                    </div>
                    <div className='col-2' >
                        <label htmlFor='semilla'>Constante:</label>
                        <input id='semilla' type='number' className="form-control" value={seed2} min='100' max='999999' onChange={(e) => setSeed2(e.target.value)} />
                    </div>
                    <div className='col-2'>
                        <label htmlFor='generar'>Numeros a Generar:</label>
                        <input id='generar' type='number' className="form-control" value={amount} onChange={(e) => setAmount(e.target.value)} />
                    </div>
                    <div className='col-2'>
                        <br />
                        <div className='btn btn-outline-primary' style={{ marginTop: "5%" }} onClick={(e) => generateNums()}>Generar</div>
                    </div>
                </div>
            </div>
            <br />
            <div className='row'>
                <div className='col-12' >
                    <table className='table'>
                        <thead>
                            <tr>
                                <th scope="col">Xi</th>
                                <th scope="col">Yi</th>
                                <th scope="col">Centro</th>
                                <th scope="col">Ri</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                generatedNums.map((e) => {
                                    return (
                                        <tr scope="row">
                                            <td>{e.w}</td>
                                            <td>{e.x}</td>
                                            <td>{e.y}</td>
                                            <td>{e.z}</td>
                                        </tr>
                                    )
                                }
                                )
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default MultiplicadorConstante