const getCentro = (numero, digitos) => {
    numero += '';
    let textoNumero = (numero.length - digitos) % 2 == 0 ? '' + numero : '0' + numero;
    let largo = Math.floor((textoNumero.length - digitos) / 2);
    return textoNumero.slice(largo, largo + digitos);
}

const transformToR = (x, d) => (x * Math.pow(10, -d)).toFixed(d);

module.exports = { getCentro, transformToR };