import React, { useState, useEffect } from 'react'
import { Jumbotron } from 'react-bootstrap'

const Huecos = () => {
    let [rawList, setRawList] = useState("");
    let [minInter, setMinInter] = useState(0.5);
    let [maxInter, setMaxInter] = useState(1);
    let [table, setTable] = useState([]);
    let [H, setH] = useState(0);
    let [estTotal, setEstTotal] = useState(0)
    let [display, setDisplay] = useState(false);
    const X2 = 11.07

    const calculate = () => {
        let list = inputToList()
        let normList = normalizeList(list)
        let table = fillTable(normList)

        setTable(table)
        setDisplay(true)
    }

    const inputToList = () => {
        let cleanedList = rawList.split(',').map((x) => {
            return x.trim()
        })
        return cleanedList
    }

    // agrega 1 si el numero esta dentro de los intervalos, 0 si no
    const normalizeList = (list) => {
        let newList = []
        list.forEach((x) => {
            if (minInter <= x && x <= maxInter) {
                newList.push(1)
            } else {
                newList.push(0)
            }
        })
        return newList
    }

    // llama a todos los metodos necesarios para llenar la tabla
    const fillTable = (list) => {
        let { firstTable, H } = getOi(list)
        setH(H)
        let table = getEi(firstTable, H)
        return getEstadistico(table)
    }

    // cuenta los hoyos con un tamao dado
    const getOi = (list) => {
        console.log(list)
        let firstOne = list.indexOf(1)
        let H = 0
        let table = [
            { i: 0, oi: 0, ei: 0, f: 0 },
            { i: 1, oi: 0, ei: 0, f: 0 },
            { i: 2, oi: 0, ei: 0, f: 0 },
            { i: 3, oi: 0, ei: 0, f: 0 },
            { i: 4, oi: 0, ei: 0, f: 0 },
            { i: 5, oi: 0, ei: 0, f: 0 }
        ]
        for (let h = 0; h <= 5; h++) {
            let holes = 0
            for (let i = firstOne; i < list.length - 1; i++) {
                for (let k = i + 1; k < list.length; k++) {
                    if (list[k] === 1) {
                        if (list[i] === list[k]) {
                            if (h === holes) {
                                table[h].oi += 1
                                H++
                            } else if (h === 5 && holes >= 5) {
                                table[h].oi += 1
                                H++
                            }
                            i = k
                        }
                        holes = 0
                    } else {
                        holes++
                    }

                }
            }
        }
        return { firstTable: table, H: H }
    }

    // set values for the EI column
    const getEi = (table, H) => {
        table.forEach((obj) => {
            obj.ei = H * (maxInter - minInter) * Math.pow((1 - (maxInter - minInter)), obj.i)
        })
        return table
    }

    // set values for the Last column
    const getEstadistico = (table) => {
        let total = 0
        table.forEach((obj) => {
            obj.f = Math.pow((obj.ei - obj.oi), 2) / obj.ei
            total += obj.f
        })
        setEstTotal(total)
        return table
    }

    const getResultMessage = () => {
        if (display) {
            if (estTotal < X2) {
                return "Debido a que X^2 calculada es menor a X^2 de la tabla, no se puede rechazar que los nmeros son independientes"
            } else {
                return "Debido a que X^2 calculada es mayor a X^2 de la tabla, se puede rechazar que los nmeros son independientes"
            }
        }
        return '';
    }

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Prueba de Huecos
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div className='form-group'>
                <div className="row">
                    <div className="col-2">
                        <label htmlFor='intervalo0'>Intervalo alpha:</label>
                        <input id='intervalo0' type='number' className="form-control" value={minInter} min='0' max='1' onChange={(e) => setMinInter(e.target.value * 1)} />
                    </div>
                    <div className="col-2">
                        <label htmlFor='interval1'>Intervalo beta:</label>
                        <input id='intervalo1' type='number' className="form-control" value={maxInter} min='0' max='1' onChange={(e) => setMaxInter(e.target.value * 1)} />
                    </div>
                    <div className='col-8'>
                        <p>H0: Los numeros del conjunto ri son independientes</p>
                        <p>H1: Los numeros del conjunto ri no son independientes</p>
                    </div>
                </div>
                <p>Nivel de confianza: 95%</p>
                <div className='row'>
                    <div className='col-12' >
                        <label htmlFor='numeros'>Numeros:</label>
                        <textarea id='list' className="form-control" rows="4" onChange={(e) => setRawList(e.target.value)} />
                        <p><small>Ingresa los numeros separados por comas</small></p>
                    </div>
                </div>
                <div className='row text-right'>
                    <div className='col-12'>
                        <div className='btn btn-outline-primary' onClick={(e) => calculate()}>Correr Prueba</div>
                    </div>
                </div>

            </div>

            <div className="row">
                <div className='col-12'>
                    <table className='table text-center'>
                        <thead>
                            <tr>
                                <th scope="col">X^2 Tabla</th>
                            </tr>
                        </thead>
                        <tbody>

                            <tr scope="row">
                                <td><b>{X2}</b></td>
                            </tr>
                        </tbody>
                    </table>
                    <table className='table text-center'>
                        <thead>
                            <tr>
                                <th scope="col">i</th>
                                <th scope="col">Oi</th>
                                <th scope="col">Ei</th>
                                <th scope="col">X^2</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                table.map((e) => {
                                    return (
                                        <tr scope="row" key={e.i}>
                                            <td>{e.i}</td>
                                            <td>{e.oi}</td>
                                            <td>{e.ei.toFixed(4)}</td>
                                            <td>{e.f.toFixed(4)}</td>
                                        </tr>
                                    )
                                })
                            }
                            <tr scope="row">
                                <td>Total</td>
                                <td>H={H}</td>
                                <td>H={H}</td>
                                <td><b>{estTotal != 0 ? estTotal.toFixed(4) : 0}</b></td>
                            </tr>
                        </tbody>
                    </table>

                    <br />
                    {(() => {
                        if (getResultMessage() !== '') {
                            return (<Jumbotron>
                                <p className="text-center">{getResultMessage()}</p>
                            </Jumbotron>)
                        }
                    })()}
                </div>
            </div>

        </div>
    );
};

export default Huecos