import React, { useState, useEffect } from "react";
import chiSquareInverse from "inv-chisquare-cdf";
import { Jumbotron } from 'react-bootstrap'

const Series = () => {
    let [cuadrants, setCuadrants] = useState([]);
    let [c, setC] = useState(0.95);
    let [m, setM] = useState(0);
    let [currentNum, setCurrentNum] = useState("");
    let [numbersCSVString, setNumbersCSVString] = useState("");
    let [numbers, setNumbers] = useState([]);
    let [numbersLength, setNumbersLength] = useState(0);
    let [x2Total, setX2Total] = useState(0);
    let [acepta, setAcepta] = useState(false);
    let [testRun, setTestRun] = useState(false);
    let [oiTotal, setOiTotal] = useState(0);
    let [eiTotal, setEiTotal] = useState(0);
    let [alpha, setAlpha] = useState(0.05);
    let [chiValue, setChiValue] = useState(0);
    let [testTable, setTestTable] = useState([]);

    useEffect(() => {
        if (chiValue > x2Total) {
            setAcepta(true);
        }
        if (testTable.length > 0) setTestRun(true);
    }, [x2Total, chiValue, testTable.length]);

    useEffect(() => {
        let degreeFreed = m > 2 ? m - 1 : 1;
        setChiValue(chiSquareInverse.invChiSquareCDF(1 - alpha, degreeFreed));
    }, [alpha, m]);

    const closestSquareTable = (m) => {
        let notFound = true;
        let i = 0;
        while (notFound) {
            let ibase2 = Math.pow(i, 2);
            if (m > ibase2) {
                i++;
            } else if (m < ibase2) {
                m = ibase2;
                notFound = false;
            } else if (m == ibase2) {
                notFound = false;
            }
        }
        return m;
    };

    const calculateSeriesTest = () => {
        let nums;
        let tempNumberlenght;
        if (parseFloat(c)) {
            nums = numbersCSVString.split(",").map((e) => parseFloat(e));
            setAlpha(1 - parseFloat(c));
            setNumbers([...nums]);
            setNumbersLength(nums.length);
        } else {
            alert("Ingresa un nivel de confianza mayor a cero")
            return;
        }
        tempNumberlenght = nums.length;

        if (nums.length > 0) {
            let divs = Math.sqrt(tempNumberlenght);
            let tempM = closestSquareTable(divs);
            console.log(tempM)
            let coordinates = [];

            setM(tempM);
            let ei = (tempNumberlenght - 1) / tempM;
            for (let i = 0; i < tempNumberlenght - 1; i++) {
                coordinates.push({ xValue: nums[i], yValue: nums[i + 1] });
            }
            let base = Math.sqrt(tempM);
            let coordX = 0;
            let coordY = 0;
            let range = 1 / base;
            let tableCuadrnts = [];
            for (let i = 0; i < base; i++) {
                for (let j = 0; j < base; j++) {
                    tableCuadrnts.push({
                        lowerX: coordX,
                        higherX: coordX + range,
                        lowerY: coordY,
                        higherY: coordY + range,
                    });
                    coordX += range;
                }
                coordY += range;
                coordX = 0;
            }
            setCuadrants(tableCuadrnts);
            console.log(tableCuadrnts)

            let ammountByCuadrant = [];
            for (let i = 0; i < tableCuadrnts.length; i++) {
                for (let j = 0; j < coordinates.length; j++) {
                    if (
                        coordinates[j].xValue > tableCuadrnts[i].lowerX &&
                        coordinates[j].xValue < tableCuadrnts[i].higherX &&
                        coordinates[j].yValue > tableCuadrnts[i].lowerY &&
                        coordinates[j].yValue < tableCuadrnts[i].higherY
                    ) {
                        ammountByCuadrant.push(i);
                    }
                }
            }

            let tempOi = new Array(tableCuadrnts.length).fill(0);

            for (let i = 0; i < ammountByCuadrant.length; i++) {
                tempOi[ammountByCuadrant[i]]++;
            }

            let tempx2Vals = [];
            let tempx2TotalVal = 0;
            let oitotal = 0;
            let eitotal = ei * tempM;
            for (let i = 0; i < tempM; i++) {
                oitotal += tempOi[i];
                tempx2Vals[i] = parseFloat(Math.pow(ei - tempOi[i], 2) / ei);
                tempx2TotalVal += tempx2Vals[i];
            }
            setX2Total(tempx2TotalVal);
            setOiTotal(oitotal);
            setEiTotal(eitotal)

            let tempTestTable = [];
            for (let i = 0; i < tableCuadrnts.length; i++) {
                tempTestTable.push({
                    interval: i + 1,
                    oi: tempOi[i],
                    ei: ei,
                    x2: tempx2Vals[i],
                });
            }
            setTestTable(tempTestTable);
        }
    };

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Prueba de Series
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div className="form-group">
                <div className="row">
                    <div className="col-2">
                        <label for="semilla">Nivel de cofianza:</label>
                        <input id="semilla" type="number" value={c} className="form-control" onChange={(e) => setC(e.target.value)} />
                        <p><small>Ingresa en forma decimal</small></p>
                    </div>
                    <div className='col-8'>
                        <p>H0: Los numeros del conjunto ri son independientes</p>
                        <p>H1: Los numeros del conjunto ri no son independientes</p>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12">
                        <label for="csv">Numeros:</label>
                        <textarea id="csv" type="text" value={numbersCSVString} className="form-control" rows="4" onChange={(e) => setNumbersCSVString(e.target.value)} />
                        <p><small>Ingresa los numeros separados por comas</small></p>
                    </div>
                </div>
            </div>
            <div className='row text-right'>
                <div className='col-12'>
                    <div className='btn btn-outline-primary' onClick={(e) => calculateSeriesTest()}>Correr Prueba</div>
                </div>
            </div>
            <br />
            <div className="row">
                <div className='col-12'>
                    <table className='table text-center'>
                        <thead>
                            <tr>
                                <th scope="col">X^2 Tabla</th>
                            </tr>
                        </thead>
                        <tbody>

                            <tr scope="row">
                                <td><b>{chiValue != 0 ? chiValue.toFixed(4) : 0}</b></td>
                            </tr>
                        </tbody>
                    </table>
                    <table className="table text-center">
                        <thead>
                            <tr>
                                <th scope="col">Intervalo</th>
                                <th scope="col">Oi</th>
                                <th scope="col">Ei = n-1/m</th>
                                <th scope="col">(Ei-Oi)^2/Ei</th>
                            </tr>
                        </thead>
                        <tbody>
                            {testTable.map((e) => {
                                {
                                    return (
                                        <tr scope="row">
                                            <td>{e.interval}</td>
                                            <td>{e.oi}</td>
                                            <td>{e.ei.toFixed(2)}</td>
                                            <td>{e.x2.toFixed(4)}</td>
                                        </tr>
                                    );
                                }
                            })}
                            <tr scope="row">
                                <td>Total</td>
                                <td>{oiTotal}</td>
                                <td>{eiTotal}</td>
                                <td><b>{x2Total != 0 ? x2Total.toFixed(4) : 0}</b></td>
                            </tr>
                        </tbody>
                    </table>
                    <br />
                    {(() => {
                        if (testRun) {
                            if (acepta) {
                                return (<Jumbotron>
                                    <p className="text-center">Como el valor X^2 calculado es menor a X^2 de la tabla, no podemos rechazar que los nmeros son independientes</p>
                                </Jumbotron>)
                            }
                            return (<Jumbotron>
                                <p className="text-center">Como el valor X^2 calculado es mayor a X^2 de la tabla, podemos rechazar que los nmeros son independientes</p>
                            </Jumbotron>)
                        }
                    })()}
                </div>
            </div>
        </div>
    );
};

export default Series;
