import React, { useState, useEffect } from 'react'
import { Jumbotron } from 'react-bootstrap'


const CongruencialMultiplicativo = () => {
    let [d, setD] = useState(4);
    let [seed, setSeed] = useState(0);
    let [k, setK] = useState(0);
    let [g, setG] = useState(0);
    let [amount, setAmount] = useState(0);
    let [generatedNums, setGeneratedNums] = useState([]);

    const constuct = (x, y, z) => {
        return {
            x,
            y,
            z
        };
    };
    const generateNums = () => {
        let a = parseInt(g) + (8 * k);
        let m = Math.pow(2, g);
        let tempArr = [];
        let seedSquare = ((parseInt(a) * seed)) % m;
        tempArr.push(constuct(seed, seedSquare, (seedSquare / (m - 1)).toFixed(d)));
        for (let i = 1; i < amount; i++) {
            seedSquare = (a * tempArr[i - 1].y) % m;
            tempArr.push(constuct(tempArr[i - 1].y, seedSquare, (seedSquare / (m - 1)).toFixed(d)));
        }
        setGeneratedNums(tempArr);
    }

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Algoritmo Congruencial Multiplicativo
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div>
                <div className='row'>
                    <div className='col-2' >
                        <label htmlFor='semilla'>X0:</label>
                        <input id='semilla' className="form-control" type='number' value={seed} min='100' max='999999' onChange={(e) => setSeed(e.target.value)} />
                    </div>
                    <div className='col-2' >
                        <label htmlFor='semilla'>k:</label>
                        <input id='semilla' className="form-control" type='number' value={k} min='100' max='999999' onChange={(e) => setK(e.target.value)} />
                    </div>
                    <div className='col-2' >
                        <label htmlFor='semilla'>g:</label>
                        <input id='semilla' className="form-control" type='number' value={g} min='100' max='999999' onChange={(e) => setG(e.target.value)} />
                    </div>
                    <div className='col-2'>
                        <label htmlFor='generar'>Numeros a Generar:</label>
                        <input id='generar' className="form-control" type='number' value={amount} onChange={(e) => setAmount(e.target.value)} />
                    </div>
                    <div className='col-2'>
                        <br />
                        <div className='btn btn-outline-primary' style={{ marginTop: "5%" }} onClick={(e) => generateNums()}>Generar</div>
                    </div>
                </div>
            </div>
            <br />
            <div className='row'>
                <div className='col-12' >
                    <table className='table'>
                        <thead>
                            <tr>
                                <th scope="col">Xi-1</th>
                                <th scope="col">Xi</th>
                                <th scope="col">Ri</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                generatedNums.map((e) => {
                                    return (
                                        <tr scope="row">
                                            <td>{e.x}</td>
                                            <td>{e.y}</td>
                                            <td>{e.z}</td>
                                        </tr>
                                    )
                                }
                                )
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default CongruencialMultiplicativo