import React, { useState } from 'react'
import { Jumbotron } from 'react-bootstrap'
import { standardNormalTable } from 'simple-statistics'

const Medias = () => {
    let [numeros, setNumeros] = useState('');
    let [N, setN] = useState(0);
    let [c, setC] = useState(0.95);
    let [alpha, setAlpha] = useState(0.05)
    let [LI, setLI] = useState(0);
    let [LS, setLS] = useState(0);
    let [R, setR] = useState(0);
    let [Hipot, setHipot] = useState('');

    const prueba = () => {
        if (parseFloat(c) < 0 || parseFloat(c) > 1) {
            alert('Inserta un nivel de aceptacion entre 0 y 1')
            return;
        }

        let temp = 0;
        let flag = true;
        standardNormalTable.forEach((val, index) => {
            if (val >= 1 - ((1 - parseFloat(c)) / 2) && flag) {
                temp = index;
                flag = false;
            }
        });

        let tempArr = numeros.split(',');
        let n = tempArr.length
        setN(n)
        let media = tempArr.reduce((a, b) => {
            return a + parseFloat(b.trim())
        }, 0.0) / n;
        setR((media).toFixed(4));
        setAlpha((1 - parseFloat(c)).toFixed(2))

        let temp2 = (temp * .01) / (Math.sqrt(12 * n));
        setLI((.5 - temp2).toFixed(4));
        setLS((.5 + temp2).toFixed(4));

        let hipot = .5 + temp2 > parseFloat(media) && parseFloat(media) > .5 - temp2 ? "Como nuestro , se encuentra dentro de los lmites, no se puede rechazar el planteamiento de que el conjunto de nmeros tiene media 0.5" :
            "Como nuestro , no se encuentra dentro de los lmites, se puede rechazar el planteamiento de que el conjunto de nmeros tiene media 0.5";
        setHipot(hipot)
    }

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Prueba de Medias
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div className='form-group'>
                <div className='row'>
                    <div className='col-2' >
                        <label htmlFor='semilla'>Nivel de confianza:</label>
                        <input id='semilla' type='text' className="form-control" value={c} onChange={(e) => setC(e.target.value)} />
                        <p><small>Ingresa en forma decimal</small></p>
                    </div>
                    <div className='col-10'>
                        <p>H0: Los numeros del conjunto ri tienen una media de 0.5</p>
                        <p>H1: Los numeros del conjunto ri no tienen una media de 0.5</p>
                    </div>
                </div>

                <div className='row'>
                    <div className='col-12' >
                        <label htmlFor='numeros'>Numeros:</label>
                        <textarea id='numeros' className="form-control" rows="4" onChange={(e) => setNumeros(e.target.value)} />
                        <p><small>Ingresa los numeros separados por comas</small></p>
                    </div>
                </div>
            </div>
            <div className='row text-right'>
                <div className='col-12'>
                    <div className='btn btn-outline-primary' onClick={(e) => prueba()}>Correr Prueba</div>
                </div>
            </div>
            <br />
            <div className='row'>
                <div className='col-12'>
                    <table className="table text-center">
                        <thead>
                            <tr>
                                <th scope="col">Alpha</th>
                                <th scope="col"></th>
                                <th scope="col">1</th>
                                <th scope="col"></th>
                                <th scope="col">LI</th>
                                <th scope="col">LS</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>{alpha}</td>
                                <td>=0.5</td>
                                <td>0.5</td>
                                <td>{R}</td>
                                <td>{LI}</td>
                                <td>{LS}</td>
                            </tr>
                        </tbody>
                    </table>
                    <br />
                    {(() => {
                        if (Hipot !== '') {
                            return (<Jumbotron>
                                <p className="text-center">{Hipot}</p>
                            </Jumbotron>)
                        }
                    })()}
                </div>
            </div>

        </div >
    )
}

export default Medias