import React from 'react';
import { Switch, Route } from 'react-router-dom'
import CorridaArribaAbajoMedia from './corridaArribaAbajoMedia';
import CuadradosMedios from './cuadradosMedios';
import ProductosMedios from './productosMedios';
import MultiplicadorConstante from './multiplicadorConstante';
import Lineal from './lineal';
