import React, { useState, useEffect } from 'react'
import modifyNums from '../services/util'
import { Jumbotron } from 'react-bootstrap'


const CuadradosMedios = () => {
    let [d, setD] = useState(0);
    let [seed, setSeed] = useState(0);
    let [amount, setAmount] = useState(0);
    let [generatedNums, setGeneratedNums] = useState([]);
    useEffect(() => {
        let seedLength = seed.toString().length;
        setD(seedLength);
    }, [seed])
    const constuct = (w, x, y, z) => {
        return {
            w,
            x,
            y,
            z
        };
    };
    const generateNums = () => {
        let tempArr = [];
        let seedSquare = Math.pow(seed, 2);
        let middleSeedValues = modifyNums.getCentro(seedSquare, d);
        console.log(middleSeedValues);
        tempArr.push(constuct(seed, seedSquare, middleSeedValues, modifyNums.transformToR(middleSeedValues, d)));
        for (let i = 1; i < amount; i++) {
            seedSquare = Math.pow(tempArr[i - 1].y, 2);
            middleSeedValues = modifyNums.getCentro(seedSquare, d);
            tempArr.push(constuct(tempArr[i - 1].y, seedSquare, middleSeedValues, modifyNums.transformToR(middleSeedValues, d)));
        }
        setGeneratedNums(tempArr);
    }

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Algoritmo Cuadrados Medios
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div className='row'>
                <div className='col-2' >
                    <label htmlFor='semilla'>Semilla:</label>
                    <input id='semilla' type='number' className="form-control" value={seed} min='100' max='999999' onChange={(e) => setSeed(e.target.value)} />
                </div>
                <div className='col-2'>
                    <label htmlFor='generar'>Numeros a Generar:</label>
                    <input id='generar' type='number' className="form-control" value={amount} onChange={(e) => setAmount(e.target.value)} />
                </div>
                <div className='col-2'>
                    <br />
                    <div className='btn btn-outline-primary' style={{ marginTop: "5%" }} onClick={(e) => generateNums()}>Generar</div>
                </div>
            </div><br />
            <div className='row'>
                <div className='col-12' >
                    <table className='table'>
                        <thead>
                            <tr>
                                <th scope="col">Xi</th>
                                <th scope="col">Xi^2</th>
                                <th scope="col">Centro</th>
                                <th scope="col">Ri</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                generatedNums.map((e) => {
                                    return (
                                        <tr scope="row">
                                            <td>{e.w}</td>
                                            <td>{e.x}</td>
                                            <td>{e.y}</td>
                                            <td>{e.z}</td>
                                        </tr>
                                    )
                                }
                                )
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default CuadradosMedios