import React, { useState, useEffect } from 'react'
import { Jumbotron, InputGroup } from 'react-bootstrap'


const CongruencialAditivo = () => {
    let [d, setD] = useState(4);
    let [seed, setSeed] = useState(0);
    let [m, setM] = useState(0);
    let [amount, setAmount] = useState(0);
    let [generatedNums, setGeneratedNums] = useState([]);

    const constuct = (w, x, y, z) => {
        return {
            w,
            x,
            y,
            z
        };
    };
    const generateNums = () => {
        let tempArr = seed.split(',');

        if (tempArr.length < 5) {
            alert('Debes ingresar al menos 5 numeros')
            return;
        }
        let k = tempArr.length - 1
        let arr = tempArr.map((x) => {
            return parseInt(x.trim());
        })
        tempArr = [];
        for (let i = 0; i < amount; i++) {
            let seedSquare = (arr[i + k] + arr[i]) % m;
            arr.push(seedSquare)
            tempArr.push(constuct(arr[i + k], arr[i], seedSquare, (seedSquare / (m - 1)).toFixed(d)));
        }
        setGeneratedNums(tempArr);
    }

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Algoritmo Congruencial Aditivo
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div>
                <div className='row'>
                    <div className='col-8' >
                        <label htmlFor='semilla'>Lista de numeros:</label>
                        <input id='semilla' value={seed} className="form-control" min='100' max='999999' onChange={(e) => setSeed(e.target.value)} />
                        <p><small>Ingresa los numeros separados por comas</small></p>
                    </div>
                    <div className='col-2' >
                        <label htmlFor='semilla'>M:</label>
                        <input id='semilla' type='number' className="form-control" value={m} min='100' max='999999' onChange={(e) => setM(e.target.value)} />
                    </div>
                    <div className='col-2'>
                        <label htmlFor='generar'>Numeros a Generar:</label>
                        <input id='generar' type='number' className="form-control" value={amount} onChange={(e) => setAmount(e.target.value)} />
                    </div>
                    <div className='col-2'>
                        <div className='btn btn btn-outline-primary' onClick={(e) => generateNums()}>Generar</div>
                    </div>
                </div>
            </div>
            <br />
            <div className='row'>
                <div className='col-12' >
                    <table className='table'>
                        <thead>
                            <tr>
                                <th scope="col">Xn</th>
                                <th scope="col">Xn+k</th>
                                <th scope="col">Xn+1</th>
                                <th scope="col">Ri</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                generatedNums.map((e) => {
                                    return (
                                        <tr scope="row">
                                            <td>{e.w}</td>
                                            <td>{e.x}</td>
                                            <td>{e.y}</td>
                                            <td>{e.z}</td>
                                        </tr>
                                    )
                                }
                                )
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default CongruencialAditivo