import React, { useState } from 'react'
import { Jumbotron } from 'react-bootstrap'
import { standardNormalTable } from 'simple-statistics'


const CorridaArribaAbajoMedia = () => {
    let [numeros, setNumeros] = useState('');
    let [c, setC] = useState(0.95);
    let [alpha, setAlpha] = useState(0.05)
    let [N, setN] = useState(0);
    let [C0, setC0] = useState(0);
    let [N1, setN1] = useState(0);
    let [N0, setN0] = useState(0);
    let [MC0, setMC0] = useState(0);
    let [VariazaC0, setVariazaC0] = useState(0);
    let [Z0, setZ0] = useState(0);
    let [Hipot, setHipot] = useState('');

    const prueba = () => {
        let tempArr = numeros.split(',');
        let n = tempArr.length
        setN(n)
        let media = 0.5;
        let countSwitch = 0;
        let swithflag = 0;

        let temp;
        let flag = true;
        standardNormalTable.forEach((val, index) => {
            console.log(val)
            if (val >= 1 - ((1 - parseFloat(c)) / 2) && flag) {
                temp = index;
                flag = false;
            }
        });

        let count1 = tempArr.reduce((a, b) => {
            let temp = parseFloat(b.trim()) > media ? 1 : 0
            if (temp != swithflag) {
                countSwitch++;
                swithflag = !swithflag;
            }
            return parseInt(a) + temp;
        }, 0)
        let count0 = n - count1

        setC0(countSwitch)
        setN1(count1)
        setN0(count0);
        let mc0 = ((2 * count1 * count0) / n) + (1 / 2);
        setMC0(mc0.toFixed(4));

        let variazaC0 = ((2 * count0 * count1) * (2 * count0 * count1 - n)) / ((n * n) * (n - 1))
        setVariazaC0(variazaC0.toFixed(4))

        temp = (temp * .01).toFixed(2);

        let z0 = Math.abs((countSwitch - mc0) / Math.sqrt(variazaC0))
        setZ0(z0.toFixed(4))

        let hipot = temp > z0 && z0 > -temp ? "Como el valor Zo cae dentro de nuestro intervalo (-" + temp + ", " + temp + ") no podemos rechazar que los nmeros sean independientes" :
            "Como el valor Zo no cae dentro de nuestro intervalo (-" + temp + ", " + temp + ") podemos concluir que los nmeros no son independientes";
        setHipot(hipot)
    }

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Prueba independencia Corridas Arriba y Abajo Media
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div className='form-group'>
                <div className='row'>
                    <div className='col-2' >
                        <label htmlFor='semilla'>Nivel de confianza:</label>
                        <input id='semilla' type='text' className="form-control" value={c} onChange={(e) => setC(e.target.value)} />
                        <p><small>Ingresa en forma decimal</small></p>
                    </div>
                    <div className='col-10'>
                        <p>H0: Los numeros del conjunto ri son independientes</p>
                        <p>H1: Los numeros del conjunto ri no son independientes</p>
                    </div>
                </div>
                <div className='row'>
                    <div className='col-12' >
                        <label htmlFor='numeros'>Numeros:</label>
                        <textarea id='numeros' className="form-control" rows="4" onChange={(e) => setNumeros(e.target.value)} />
                        <p><small>Ingresa los numeros separados por comas</small></p>
                    </div>
                </div>
            </div>
            <div className='row text-right'>
                <div className='col-12'>
                    <div className='btn btn-outline-primary' onClick={(e) => prueba()}>Correr Prueba</div>
                </div>
            </div>
            <br />
            <div className='row'>
                <div className='col-12'>
                    <table className="table text-center">
                        <thead>
                            <tr>
                                <th scope="col">n</th>
                                <th scope="col">Co</th>
                                <th scope="col">n0</th>
                                <th scope="col">n1</th>
                                <th scope="col">Co</th>
                                <th scope="col">2Co</th>
                                <th scope="col">Zo</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>{N}</td>
                                <td>{C0}</td>
                                <td>{N0}</td>
                                <td>{N1}</td>
                                <td>{MC0}</td>
                                <td>{VariazaC0}</td>
                                <td>{Z0}</td>
                            </tr>
                        </tbody>
                    </table>
                    <br />
                    {(() => {
                        if (Hipot !== '') {
                            return (<Jumbotron>
                                <p className="text-center">{Hipot}</p>
                            </Jumbotron>)
                        }
                    })()}
                </div>
            </div>

        </div >
    )
}

export default CorridaArribaAbajoMedia