import React, { useState, useEffect } from 'react'
import modifyNums from '../services/util'
import { Jumbotron } from 'react-bootstrap'
import exponencial from '../imgs/exponencial.png';
import uniforme from '../imgs/uniforme.png';
import poisson from '../imgs/poisson.png';
import bernoulli from '../imgs/bernoulli.png';

function fac(n) {
    return (n < 2) ? 1 : fac(n - 1) * n;
}

const TransformadaInversa = () => {
    let [d, setD] = useState(0);
    let [seed, setSeed] = useState('1');
    let [amount, setAmount] = useState(0);
    let [a, setA] = useState(0);
    let [b, setB] = useState(0);
    let [lamda, setLamda] = useState(0);
    let [media, setMedia] = useState(0);
    let [mediaPiezas, setMediaPiezas] = useState(0);
    let [probaPiezas, setProbaPiezas] = useState(0);
    let [generatedNums, setGeneratedNums] = useState([]);

    useEffect(() => {
        setGeneratedNums([])
        setAmount(0)
    }, [seed])


    const generateNums = () => {
        let nums = [];
        switch (seed) {
            case '1':
                for (let index = 0; index < amount; index++) {
                    let random = Math.random()
                    nums.push({
                        pos: index + 1,
                        r: random.toFixed(3),
                        x: (parseFloat(a) + ((parseFloat(b) - parseFloat(a)) * random)).toFixed(3)
                    })

                }
                setGeneratedNums(nums);
                break;
            case '2':
                for (let index = 0; index < amount; index++) {
                    let random = Math.random()
                    nums.push({
                        pos: index + 1,
                        r: random.toFixed(2),
                        x: (-Math.log(1 - random) * (1 / lamda)).toFixed(2)
                    })
                }
                setGeneratedNums(nums);
                break;
            case '3':
                for (let index = 0; index < amount; index++) {
                    let random = Math.random()
                    nums.push({
                        pos: index + 1,
                        r: random.toFixed(2),
                        x: random > (1 - media) ? 1 : 0
                    })
                }
                setGeneratedNums(nums);
                break;
            case '4':
                let probs = [];
                for (let index = 0; index < probaPiezas; index++) {
                    let prob = (Math.pow(mediaPiezas, index) * (Math.pow(Math.E, -mediaPiezas))) / fac(index)
                    probs.push(prob)
                }

                for (let index = 0; index < amount; index++) {
                    let random = Math.random()

                    let piezas = 0;
                    let acumulado = 0;
                    for (let index2 = 0; index2 < probs.length; index2++) {
                        if (probs[index2] + acumulado > random) {
                            piezas = index2;
                            break;
                        } else {
                            acumulado += probs[index2];
                        }
                    }
                    nums.push({
                        pos: index + 1,
                        r: random.toFixed(2),
                        x: piezas
                    })

                }
                setGeneratedNums(nums);
                break;



            default:
                break;
        }
    }

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Mtodo Transformada Inversa
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div className='row'>
                <div className='col-3' >
                    <label htmlFor='semilla'>Distribucin:</label>
                    <select d='semilla' className="form-control" value={seed} onChange={(e) => setSeed(e.target.value)}>
                        <option value="1" >Distribucin Uniforme</option>
                        <option value="2" >Distribucin Exponencial</option>
                        <option value="3" >Distribucin Bernoulli</option>
                        <option value="4" >Distribucin Poisson</option>
                        <option value="5" >Distribucin Normal</option>
                    </select>
                </div>
                <div className='col-2'>
                    <label htmlFor='generar'>Numeros a Generar:</label>
                    <input id='generar' type='number' className="form-control" value={amount} onChange={(e) => setAmount(e.target.value)} />
                </div>
                <div className='col-2'>
                    <br />
                    <div className='btn btn-outline-primary' style={{ marginTop: "5%" }} onClick={(e) => generateNums()}>Generar</div>
                </div>
                <div className='col-2'>
                    {(() => {
                        if (seed == '1') {
                            return <p >Formula Uniforme:</p>;
                        }
                        if (seed == '2') {
                            return <p>Formula Exponencial:</p>;
                        }
                        if (seed == '3') {
                            return <p>Formula Bernoulli:</p>;
                        }
                        if (seed == '4') {
                            return <p>Formula Poisson:</p>;
                        }
                    })()}

                </div>
                <div className='col-2'>
                    {(() => {
                        if (seed == '1') {
                            return <img src={uniforme} alt="Logo" />
                        }
                        if (seed == '2') {
                            return <img src={exponencial} alt="Logo" />
                        }
                        if (seed == '3') {
                            return <img src={bernoulli} alt="Logo" />
                        }
                        if (seed == '4') {
                            return <img src={poisson} alt="Logo" />
                        }
                    })()}

                </div>
            </div><br />
            {(() => {
                switch (seed) {
                    case '1':
                        return <div className='row'>
                            <div className='col-2'>
                                <label htmlFor='generar'>a:</label>
                                <input id='generar' type='number' className="form-control" value={a} onChange={(e) => setA(e.target.value)} />
                            </div>
                            <div className='col-2'>
                                <label htmlFor='generar'>b:</label>
                                <input id='generar' type='number' className="form-control" value={b} onChange={(e) => setB(e.target.value)} />
                            </div>
                        </div>
                    case '2':
                        return <div className='row'>
                            <div className='col-3'>
                                <label htmlFor='generar'>:</label>
                                <input id='generar' type='number' className="form-control" value={lamda} onChange={(e) => setLamda(e.target.value)} />
                                <p><small>Ingresa la media en formato decimal</small></p>
                                <p><small>Ej. 3 min/cliente = 0.3334</small></p>
                            </div>
                        </div>
                    case '3':
                        return <div className='row'>
                            <div className='col-3'>
                                <label htmlFor='generar'>Posibilidad de fallo:</label>
                                <input id='generar' type='number' className="form-control" value={media} onChange={(e) => setMedia(e.target.value)} />
                                <p><small>Ingresa la posibilidad en formato decimal</small></p>
                            </div>
                        </div>
                    case '4':
                        return <div className='row'>
                            <div className='col-3'>
                                <label htmlFor='generar'>Media:</label>
                                <input id='generar' type='number' className="form-control" value={mediaPiezas} onChange={(e) => setMediaPiezas(e.target.value)} />
                                <p><small>Ingresa la media en formato decimal</small></p>
                                <p><small>Ej. 2 pz/hr = 2</small></p>
                            </div>
                            <div className='col-3'>
                                <label htmlFor='generar'>Numero de Probabilidad:</label>
                                <input id='generar' type='number' className="form-control" value={probaPiezas} onChange={(e) => setProbaPiezas(e.target.value)} />
                                <p><small>Ej. Probabilidad de Piezas</small></p>
                            </div>
                        </div>

                    default:
                        return null;
                }
            })()}
            <br />
            <div className='row'>
                <div className='col-12' >
                    <table className='table'>
                        <thead>
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">ri</th>
                                <th scope="col">Xi</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                generatedNums.map((e) => {
                                    return (
                                        <tr scope="row">
                                            <td>{e.pos}</td>
                                            <td>{e.r}</td>
                                            <td>{e.x}</td>
                                        </tr>
                                    )
                                }
                                )
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default TransformadaInversa