import React, { useState } from 'react'
import { Jumbotron } from 'react-bootstrap'
import { standardNormalTable } from 'simple-statistics'
import chiSquareInverse from 'inv-chisquare-cdf'

const ChiCuadrada = () => {
    let [numeros, setNumeros] = useState('');
    let [N, setN] = useState(0);
    let [c, setC] = useState(0.95);
    let [alpha, setAlpha] = useState(0.0)
    let [LI, setLI] = useState(0);
    let [LS, setLS] = useState(0);
    let [R, setR] = useState(0);
    let [Hipot, setHipot] = useState('');
    let [resultList, setResultList] = useState([]);


    const prueba = () => {
        if (parseFloat(c) < 0 || parseFloat(c) > 1) {
            alert('Inserta un nivel de aceptacion entre 0 y 1')
            return;
        }

        let tempArr = numeros.split(',');
        let n = tempArr.length
        setN(n)
        let m = (Math.pow(n, .5));

        tempArr.sort((a, b) => parseFloat(a) - parseFloat(b));

        let low = 0;
        let hight = 1;
        let ei = n / m;
        let index = 0
        let countList = []
        let interval = 1 / Math.round(m);
        let count = 0;
        tempArr.forEach(element => {
            let tempElemet = parseFloat(element)
            if (tempElemet > (low * interval) && tempElemet <= (hight * interval)) {
                count++
            } else {
                low++;
                hight++;
                countList[index] = count;
                count = 1;
                index++;
            }
        });
        countList[index] = count;

        let finalList = [];

        countList.forEach(element => {
            finalList.push({
                oi: element,
                ei: ei,
                result: (Math.pow(ei - element, 2)) / ei
            })
        });

        setResultList(finalList)
        let tempr = finalList.reduce((a, b) => {
            return a + parseFloat(b.result);
        }, 0.0).toFixed(2)
        setR(tempr);
        let tempAlp = (1 - parseFloat(c)).toFixed(2);
        let degreeFreed = m > 2 ? m - 1 : 1;
        let tempchi = chiSquareInverse.invChiSquareCDF(1 - tempAlp, degreeFreed).toFixed(2);
        setLS(tempchi);

        let hipot = parseFloat(tempr) < parseFloat(tempchi) ? "Como nuestro estadstico calculado es menor a nuestro estadstico de las tablas, no podemos rechazar que nuestros nmeros sigan una distribucin uniforme continua" :
            "Como nuestro estadstico calculado es mayor a nuestro estadstico de las tablas,  podemos rechazar que nuestros nmeros siguen una distribucin uniforme continua";
        setHipot(hipot)
    }

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Prueba de Uniformidad Chi cuadrada
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div className='form-group'>
                <div className='row'>
                    <div className='col-2' >
                        <label htmlFor='semilla'>Nivel de confianza:</label>
                        <input id='semilla' type='text' className="form-control" value={c} onChange={(e) => setC(e.target.value)} />
                        <p><small>Ingresa en forma decimal</small></p>
                    </div>
                    <div className='col-10'>
                        <p>H0: Los numeros del conjunto ri son uniformes</p>
                        <p>H1: Los numeros del conjunto ri no son uniformes</p>
                    </div>
                </div>

                <div className='row'>
                    <div className='col-12' >
                        <label htmlFor='numeros'>Numeros:</label>
                        <textarea id='numeros' className="form-control" rows="4" onChange={(e) => setNumeros(e.target.value)} />
                        <p><small>Ingresa los numeros separados por comas</small></p>
                    </div>
                </div>
            </div>
            <div className='row text-right'>
                <div className='col-12'>
                    <div className='btn btn-outline-primary' onClick={(e) => prueba()}>Correr Prueba</div>
                </div>
            </div>
            <br />
            <div className='row'>
                <div className='col-12'>
                    <table className="table text-center">
                        <thead>
                            <tr>
                                <th scope="col">Oi</th>
                                <th scope="col">EI</th>
                                <th scope="col">( )/</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                resultList.map((e) => {
                                    return (
                                        <tr scope="row">
                                            <td>{e.oi}</td>
                                            <td>{e.ei}</td>
                                            <td>{e.result}</td>
                                        </tr>
                                    )
                                }
                                )
                            }
                        </tbody>
                    </table>
                </div>
            </div>
            <br />
            <div className='row'>
                <div className='col-12'>
                    <table className="table text-center">
                        <thead>
                            <tr>
                                <th scope="col">H0</th>
                                <th scope="col">H1</th>
                                <th scope="col">2^</th>
                                <th scope="col">^2 a,m</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr scope="row">
                                <td>ri son uniformes</td>
                                <td>ri no son uniformes</td>
                                <td>{R}</td>
                                <td>{LS}</td>
                            </tr>
                        </tbody>
                    </table>
                    <br />
                    {(() => {
                        if (Hipot !== '') {
                            return (<Jumbotron>
                                <p className="text-center">{Hipot}</p>
                            </Jumbotron>)
                        }
                    })()}
                </div>
            </div>

        </div >
    )
}

export default ChiCuadrada