import React, { useState, useEffect } from 'react'
import { Jumbotron } from 'react-bootstrap'
import { Fragment } from 'react'
import erlang from '../imgs/erlang.png';
import normal from '../imgs/normalConv.png';
import bino from '../imgs/binomial.png';


function fac(n) {
    return (n < 2) ? 1 : fac(n - 1) * n;
}

const Convolucion = () => {
    let [seed, setSeed] = useState('1');
    let [amount, setAmount] = useState(0);
    let [a, setA] = useState(0);
    let [b, setB] = useState(0);
    let [media, setMedia] = useState(0);
    let [mediaPiezas, setMediaPiezas] = useState(0);
    let [probaPiezas, setProbaPiezas] = useState(0);
    let [generatedNums, setGeneratedNums] = useState([]);
    let [numeros, setNumeros] = useState('');

    useEffect(() => {
        setGeneratedNums([])
        setAmount(0)
    }, [seed])


    const generateNums = () => {
        let nums = [];
        switch (seed) {
            case '1':
                let temmp = [];
                for (let i = 0; i < a; i++) {
                    temmp[i] = [];
                    for (let e = 0; e < amount; e++) {
                        temmp[i][e] = (Math.random())
                    }
                }
                let multi = 1;
                for (let i = 0; i < amount; i++) {
                    let count = [];
                    for (let j = 0; j < a; j++) {
                        count.push((1 - temmp[j][i]).toFixed(2))
                        multi *= 1 - temmp[j][i];
                    }
                    let value = ((-b / a) * Math.log(multi)).toFixed(3);

                    nums[i] = {
                        pos: i + 1,
                        nums: count,
                        r: multi,
                        x: value
                    };
                    multi = 1;
                }
                setGeneratedNums(nums);
                break;
            case '2':
                let tempArr = numeros.split(',');
                let n = tempArr.length
                let cont = 0;
                let tope2 = n / amount;
                if (tope2 % 1 != 0) {
                    alert('Las division en entre el total de numeros a generar y el total de numeros ingresados no es un entero')
                    return;
                }
                for (let index = 1; index <= amount; index++) {
                    let tot = 0
                    for (let index2 = 0; index2 < tope2; index2++) {
                        tot += parseFloat(tempArr[index2 + cont])
                    }
                    nums.push({
                        num: index,
                        r: tot.toFixed(4),
                        r6: (tot - 6).toFixed(4),
                        n: ((tot - 6) + parseFloat(probaPiezas) + parseFloat(mediaPiezas)).toFixed(4)
                    })
                    cont += tope2;
                }

                setGeneratedNums(nums);
                break;
            case '3':
                for (let index = 1; index <= amount; index++) {
                    let tempNums = [];
                    let count = 0;
                    for (let index2 = 0; index2 < amount; index2++) {
                        let random = Math.random()
                        let res = random > (1 - media) ? 1 : 0
                        tempNums[index2] = {
                            r: random.toFixed(2),
                            be: res
                        };
                        if (res) {
                            count++;
                        }
                    }
                    nums[index - 1] = {
                        pos: index,
                        nums: tempNums,
                        x: count
                    };
                }
                setGeneratedNums(nums);
                break;
            default:
                break;
        }
    }

    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Mtodo de Convolucion
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div className='row'>
                <div className='col-3' >
                    <label htmlFor='semilla'>Distribucin:</label>
                    <select d='semilla' className="form-control" value={seed} onChange={(e) => { setGeneratedNums([]); setSeed(e.target.value) }}>
                        <option value="1" >Distribucin Erlana</option>
                        <option value="2" >Distribucin Normal</option>
                        <option value="3" >Distribucin Binomial</option>
                    </select>
                </div>
                <div className='col-2'>
                    {(() => {
                        if (seed == '3') {
                            return <label htmlFor='generar'>Tamao del Lote:</label>;
                        }
                        if (seed == '1') {
                            return <label htmlFor='generar'>Numero de Piezas:</label>;
                        }
                        return <label htmlFor='generar'>Variables a Generar:</label>
                    })()}

                    <input id='generar' type='number' className="form-control" value={amount} onChange={(e) => setAmount(e.target.value)} />
                </div>
                <div className='col-2'>
                    <br />
                    <div className='btn btn-outline-primary' style={{ marginTop: "5%" }} onClick={(e) => generateNums()}>Generar</div>
                </div>

                <div className='col-2'>
                    {(() => {
                        if (seed == '1') {
                            return <p >Formula Earl:</p>;
                        }
                        if (seed == '2') {
                            return <p>Formula Norml:</p>;
                        }
                        return <p>Formula Binom:</p>;
                    })()}

                </div>
                <div className='col-2'>
                    {(() => {
                        if (seed == '1') {
                            return <img src={erlang} alt="Logo" />
                        }
                        if (seed == '2') {
                            return <img src={normal} alt="Logo" />
                        }
                        return <img src={bino} alt="Logo" />
                    })()}

                </div>
            </div><br />
            {(() => {
                switch (seed) {
                    case '1':
                        return <div className='row'>
                            <div className='col-2'>
                                <label htmlFor='generar'>Erlang:</label>
                                <input id='generar' type='number' className="form-control" value={a} onChange={(e) => setA(e.target.value)} />
                            </div>
                            <div className='col-2'>
                                <label htmlFor='generar'>Media:</label>
                                <input id='generar' type='number' className="form-control" value={b} onChange={(e) => setB(e.target.value)} />
                            </div>
                        </div>
                    case '2':
                        return <Fragment>
                            <div className='row'>
                                <div className='col-2'>
                                    <label htmlFor='generar'>Media:</label>
                                    <input id='generar' type='number' className="form-control" value={mediaPiezas} onChange={(e) => setMediaPiezas(e.target.value)} />
                                </div>
                                <div className='col-2'>
                                    <label htmlFor='generar'>Desviacion Estandar:</label>
                                    <input id='generar' type='number' className="form-control" value={probaPiezas} onChange={(e) => setProbaPiezas(e.target.value)} />
                                </div>
                            </div>
                            <div className='row'>
                                <div className='col-12' >
                                    <label htmlFor='numeros'>Numeros:</label>
                                    <textarea id='numeros' className="form-control" rows="4" onChange={(e) => setNumeros(e.target.value)} />
                                    <p><small>Ingresa los numeros separados por comas</small></p>
                                </div>
                            </div>
                        </Fragment>
                    case '3':
                        return <div className='row'>
                            <div className='col-2'>
                                <label htmlFor='generar'>Probabilidad Def:</label>
                                <input id='generar' type='number' className="form-control" value={media} onChange={(e) => setMedia(e.target.value)} />
                            </div>
                        </div>
                    default:
                        return null;
                }
            })()}
            <br />
            <div className='row'>
                <div className='col-12' >
                    <table className='table'>
                        <thead>
                            <tr>
                                {(() => {
                                    if (seed == '3') {
                                        if (generatedNums.length > 0) {
                                            let count = 0;
                                            return <Fragment>
                                                <th scope="col">#</th>
                                                {
                                                    generatedNums[0].nums.map((e) => {
                                                        count++;
                                                        return (
                                                            <Fragment>
                                                                <th scope="col">{"R" + count}</th>
                                                                <th scope="col">{"BE" + count}</th>
                                                            </Fragment>
                                                        )
                                                    })
                                                }
                                                <th scope="col">#Dif</th>
                                            </Fragment>
                                        }
                                        return <Fragment><th scope="col">Lote</th><th scope="col">#Dif</th></Fragment>
                                    }

                                    if (seed == '1') {
                                        if (generatedNums.length > 0) {
                                            return <Fragment>
                                                <th scope="col">#</th>
                                                {
                                                    generatedNums[0].nums.map((e) => {
                                                        return (<th scope="col">1 - ri</th>)
                                                    })
                                                }
                                                <th scope="col">Min / Pieza</th>
                                            </Fragment>
                                        }
                                        return <Fragment><th scope="col">#</th><th scope="col">Min / Pieza</th></Fragment>
                                    }
                                    return <Fragment><th scope="col">#</th><th scope="col">Suma ri</th><th scope="col">Suma ri - 6</th><th scope="col">Ni</th></Fragment>
                                })()}
                            </tr>
                        </thead>
                        <tbody>
                            {
                                generatedNums.map((e) => {
                                    if (seed == '1') {
                                        return (
                                            <tr scope="row">
                                                <td>{e.pos}</td>
                                                <Fragment>
                                                    {e.nums.map((num) => {
                                                        return <td>{num}</td>
                                                    })}
                                                </Fragment>
                                                <td>{e.x}</td>
                                            </tr>
                                        )
                                    }
                                    if (seed == '2') {
                                        return (
                                            <tr scope="row">
                                                <td>{e.num}</td>
                                                <td>{e.r}</td>
                                                <td>{e.r6}</td>
                                                <td>{e.n}</td>
                                            </tr>
                                        )
                                    }
                                    return (
                                        <tr scope="row">
                                            <td>{e.pos}</td>
                                            <Fragment>
                                                {e.nums.map((num) => {
                                                    return <Fragment>
                                                        <td>{num.r}</td>
                                                        <td>{num.be}</td>
                                                    </Fragment>
                                                })}
                                            </Fragment>
                                            <td>{e.x}</td>
                                        </tr>
                                    )
                                }
                                )
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default Convolucion