import React, { useState } from "react";
import chiSquareInverse from "inv-chisquare-cdf";
import { Jumbotron } from 'react-bootstrap'

const PruebaPoker = () => {
    let [numbers, setNums] = useState([]);
    let [c, setC] = useState(0.95);
    let [numbersCSVString, setNumbersCSVString] = useState("");
    let [testRun, setTestRun] = useState(false);
    let [disable, setDisable] = useState(true);
    let [fillAlpha, setFillAlpa] = useState(false);
    let [numlist, setnumList] = useState([]);
    let [numbersLength, setNumbersLength] = useState(0);

    const [categories, setCategories] = useState({
        todoDif: 0,
        unPar: 0,
        dosPares: 0,
        unaTercia: 0,
        terciaPar: 0,
        poker: 0,
        quintilla: 0
    });
    const [ei, setEi] = useState({
        todoDif: 0,
        unPar: 0,
        dosPares: 0,
        unaTercia: 0,
        terciaPar: 0,
        poker: 0,
        quintilla: 0
    });
    const [eiOi, setEiOi] = useState({
        todoDif: 0,
        unPar: 0,
        dosPares: 0,
        unaTercia: 0,
        terciaPar: 0,
        poker: 0,
        quintilla: 0,
        total: 0
    });

    const [x20, setx20] = useState("");
    const [chi2, setChi2] = useState(0);

    const class5D = ["TD", "1P", "2P", "1T", "TP", "P", "Q"];
    const prob5D = [0.3024, 0.504, 0.108, 0.009, 0.072, 0.0045, 0.0001];

    const class3D = ["TD", "1P", "T"];
    const prob3D = [0.72, 0.27, 0.01];

    const class4D = ["TD", "1P", "2P", "1T", "P"];
    const prob4D = [0.504, 0.432, 0.027, 0.036, 0.001];

    const solve5D = () => {
        let numberList = numbersCSVString.split(",");
        setnumList(numberList);
        let noNewLine = numberList.map((x) => x.replace(/(\r\n|\n|\r)/gm, ""));

        var noDot = noNewLine.map((s) => s.substring(2));
        var sorted = noDot.map((x) => {
            let arr = x.split("");
            let sorted = arr.sort((a, b) => a - b);
            let joinArr = sorted.join("");

            return joinArr;
        });

        let alpha = (1 - parseFloat(c))

        setNums(sorted);
        setNumbersLength(sorted.length)

        let nums = sorted;

        let tempCat;
        let todoDif = 0;
        let unPar = 0;
        let dosPar = 0;
        let unaTercia = 0;
        let terciaPar = 0;
        let poker = 0;
        let quintilla = 0;

        if (nums[0].length === 5) {
            let arr = nums.map((str) => {
                let first = str.split(str[0]).length - 1;
                let second = str.split(str[1]).length - 1;
                let third = str.split(str[2]).length - 1;
                let fourth = str.split(str[3]).length - 1;
                let fifth = str.split(str[4]).length - 1;

                let all = [];
                all.push(first);
                all.push(second);
                all.push(third);
                all.push(fourth);
                all.push(fifth);

                if (all.includes(5)) {
                    quintilla++;
                } else if (all.includes(3) && all.includes(2)) {
                    terciaPar++;
                } else if (all.includes(2) && all.includes(1)) {
                    let count = 0;
                    for (let i = 0; i < all.length; i++) {
                        if (all[i] === 2) {
                            count++;
                        }
                    }
                    if (count === 4) {
                        dosPar++;
                    } else {
                        unPar++;
                    }
                } else if (all.includes(4)) {
                    poker++;
                } else if (all.includes(3) && all.includes(1)) {
                    unaTercia++;
                } else {
                    todoDif++;
                }
            });
            tempCat = {
                todoDif: todoDif,
                unPar: unPar,
                dosPares: dosPar,
                unaTercia: unaTercia,
                terciaPar: terciaPar,
                poker: poker,
                quintilla: quintilla,
            };
            setCategories(tempCat);
        } else if (nums[0].length === 4) {
            let arr = nums.map((str) => {
                let first = str.split(str[0]).length - 1;
                let second = str.split(str[1]).length - 1;
                let third = str.split(str[2]).length - 1;
                let fourth = str.split(str[3]).length - 1;

                let all = [];
                all.push(first);
                all.push(second);
                all.push(third);
                all.push(fourth);
                console.log(all);

                if (all.includes(4)) {
                    poker++;
                } else if (all.includes(3)) {
                    unaTercia++;
                } else if (all.includes(2)) {
                    let count = 0;
                    for (let i = 0; i < all.length; i++) {
                        if (all[i] === 2) {
                            count++;
                        }
                    }
                    if (count === 4) {
                        console.log("2P");
                        dosPar++;
                    } else {
                        unPar++;
                        console.log("1P");
                    }
                } else {
                    console.log("TD");
                    todoDif++;
                }
            });

            tempCat = {
                todoDif: todoDif,
                unPar: unPar,
                dosPares: dosPar,
                unaTercia: unaTercia,
                poker: poker,
                terciaPar: 0,
                quintilla: 0,
            };

            setCategories(tempCat);
        } else if (nums[0].length === 3) {
            let arr = nums.map((str) => {
                let first = str.split(str[0]).length - 1;
                let second = str.split(str[1]).length - 1;
                let third = str.split(str[2]).length - 1;

                let all = [];
                all.push(first);
                all.push(second);
                all.push(third);
                console.log(all);

                if (all.includes(3)) {
                    unaTercia++;
                } else if (all.includes(2)) {
                    unPar++;
                } else {
                    console.log("TD");
                    todoDif++;
                }
            });

            tempCat = {
                todoDif: todoDif,
                unPar: unPar,
                unaTercia: unaTercia,
                poker: poker,
                terciaPar: 0,
                quintilla: 0,
            };

            setCategories(tempCat);
        }

        if (nums[0].length === 5) {
            let ei = prob5D.map((x) => parseFloat(x) * nums.length);
            setEi({
                todoDif: ei[0],
                unPar: ei[1],
                dosPares: ei[2],
                unaTercia: ei[3],
                terciaPar: ei[4],
                poker: ei[5],
                quintilla: ei[6],
            });
            let td = Math.pow(ei[0] - tempCat.todoDif, 2) / ei[0];
            let unP = Math.pow(ei[1] - tempCat.unPar, 2) / ei[1];
            let dosP = Math.pow(ei[2] - tempCat.dosPares, 2) / ei[2];
            let unT = Math.pow(ei[3] - tempCat.unaTercia, 2) / ei[3];
            let terciaP = Math.pow(ei[4] - tempCat.terciaPar, 2) / ei[4];
            let p = Math.pow(ei[5] - tempCat.poker, 2) / ei[5];
            let q = Math.pow(ei[6] - tempCat.quintilla, 2) / ei[6];
            let total = td + unP + dosP + unT + terciaP + p + q;
            setEiOi({
                todoDif: td,
                unPar: unP,
                dosPares: dosP,
                unaTercia: unT,
                terciaPar: terciaP,
                poker: p,
                quintilla: q,
                total: total,
            });

            setx20(total);
            let chisq = chiSquareInverse.invChiSquareCDF(1 - alpha, 6);
            setChi2(chisq);
        } else if (nums[0].length === 4) {
            let ei = prob4D.map((x) => parseFloat(x) * nums.length);
            console.log("ENTRA:", ei);
            setEi({
                todoDif: ei[0],
                unPar: ei[1],
                dosPares: ei[2],
                unaTercia: ei[3],
                terciaPar: 0,
                poker: ei[4],
                quintilla: 0,
            });
            let td = Math.pow(ei[0] - tempCat.todoDif, 2) / ei[0];
            let unP = Math.pow(ei[1] - tempCat.unPar, 2) / ei[1];
            let dosP = Math.pow(ei[2] - tempCat.dosPares, 2) / ei[2];
            let unT = Math.pow(ei[3] - tempCat.unaTercia, 2) / ei[3];
            let terciaP = 0;
            let p = Math.pow(ei[4] - tempCat.poker, 2) / ei[4];
            let q = 0;
            let total = td + unP + dosP + unT + terciaP + p + q;
            setEiOi({
                todoDif: td,
                unPar: unP,
                dosPares: dosP,
                unaTercia: unT,
                terciaPar: 0,
                poker: p,
                quintilla: 0,
                total: total,
            });
            console.log(total);
            setx20(total);
            let chisq = chiSquareInverse.invChiSquareCDF(1 - alpha, 6);
            setChi2(chisq);
            console.log(chisq);
        } else if (nums[0].length === 3) {
            let ei = prob3D.map((x) => parseFloat(x) * nums.length);
            setEi({
                todoDif: ei[0],
                unPar: ei[1],
                dosPares: 0,
                unaTercia: ei[2],
                terciaPar: 0,
                poker: 0,
                quintilla: 0,
            });
            let td = Math.pow(ei[0] - tempCat.todoDif, 2) / ei[0];
            let unP = Math.pow(ei[1] - tempCat.unPar, 2) / ei[1];
            let dosP = 0;
            let unT = Math.pow(ei[2] - tempCat.unaTercia, 2) / ei[2];
            let terciaP = 0;
            let p = 0;
            let q = 0;
            let total = td + unP + dosP + unT + terciaP + p + q;
            setEiOi({
                todoDif: td,
                unPar: unP,
                dosPares: 0,
                unaTercia: unT,
                terciaPar: 0,
                poker: 0,
                quintilla: 0,
                total: total,
            });

            setx20(total);
            let chisq = chiSquareInverse.invChiSquareCDF(1 - alpha, 6);
            setChi2(chisq);
        }
        setTestRun(true);
    };


    return (
        <div>
            <div className='row'>
                <Jumbotron>
                    <h1>
                        Prueba de Poker
                    </h1>
                </Jumbotron>
            </div>
            <br />
            <div className="form-group">
                <div className="row">
                    <div className="col-2">
                        <label for="semilla">Nivel de cofianza:</label>
                        <input id="semilla" type="number" value={c} className="form-control" onChange={(e) => setC(e.target.value)} />
                        <p><small>Ingresa en forma decimal</small></p>
                    </div>
                    <div className='col-8'>
                        <p>H0: Los numeros del conjunto ri son independientes</p>
                        <p>H1: Los numeros del conjunto ri no son independientes</p>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12">
                        <label for="csv">Numeros:</label>
                        <textarea id="csv" type="text" value={numbersCSVString} className="form-control" rows="4" onChange={(e) => setNumbersCSVString(e.target.value)} />
                        <p><small>Ingresa los numeros separados por comas</small></p>
                    </div>
                </div>
            </div>
            <div className='row text-right'>
                <div className='col-12'>
                    <div className='btn btn-outline-primary' onClick={(e) => solve5D()}>Correr Prueba</div>
                </div>
            </div>
            <br />
            <div className="row">
                <div className='col-12'>
                    <table className='table text-center'>
                        <thead>
                            <tr>
                                <th scope="col">X^2 Tabla</th>
                            </tr>
                        </thead>
                        <tbody>

                            <tr scope="row">
                                <td><b>{chi2 != 0 ? chi2.toFixed(4) : 0}</b></td>
                            </tr>
                        </tbody>
                    </table>
                    <table className="table text-center">
                        <thead>
                            <tr>
                                <th className=" text-left" scope="col">Categorias</th>
                                <th scope="col">Oi</th>
                                <th scope="col">Ei</th>
                                <th scope="col"> (Ei-Oi)^2/Ei </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td className=" text-left">Todos diferentes (TD)</td>
                                <td>{categories.todoDif}</td>
                                <td>{ei.todoDif.toFixed(4)}</td>
                                <td>{eiOi.todoDif.toFixed(4)}</td>
                            </tr>
                            <tr>
                                <td className=" text-left">Un par (1P)</td>
                                <td>{categories.unPar}</td>
                                <td>{ei.unPar.toFixed(4)}</td>
                                <td>{eiOi.unPar.toFixed(4)}</td>
                            </tr>
                            <tr>
                                <td className=" text-left">Dos Pares (2P)</td>
                                <td>{categories.dosPares}</td>
                                <td>{ei.dosPares.toFixed(4)}</td>
                                <td>{eiOi.dosPares.toFixed(4)}</td>
                            </tr>
                            <tr>
                                <td className=" text-left">Tercia (T)</td>
                                <td>{categories.unaTercia}</td>
                                <td>{ei.unaTercia.toFixed(4)}</td>
                                <td>{eiOi.unaTercia.toFixed(4)}</td>
                            </tr>
                            <tr>
                                <td className=" text-left">1 Tercia y un par (TP)</td>
                                <td>{categories.terciaPar}</td>
                                <td>{categories.terciaPar.toFixed(4)}</td>
                                <td>{eiOi.terciaPar.toFixed(4)}</td>
                            </tr>
                            <tr>
                                <td className=" text-left">Poker (P)</td>
                                <td>{categories.poker}</td>
                                <td>{ei.poker.toFixed(4)}</td>
                                <td>{eiOi.poker.toFixed(4)}</td>
                            </tr>
                            <tr>
                                <td className=" text-left">Qintilla (Q)</td>
                                <td>{categories.quintilla}</td>
                                <td>{ei.quintilla.toFixed(4)}</td>
                                <td>{eiOi.quintilla.toFixed(4)}</td>
                            </tr>
                            <tr>
                                <td className=" text-left">Total</td>
                                <td>{numbersLength}</td>
                                <td>{numbersLength}</td>
                                <td><b>{eiOi.total.toFixed(4)}</b></td>
                            </tr>
                        </tbody>
                    </table>
                    <br />
                    {(() => {
                        if (testRun) {
                            if (chi2 > x20) {
                                return (<Jumbotron>
                                    <p className="text-center">Como el valor X^2 calculado es menor a X^2 de la tabla, no podemos rechazar que los nmeros son independientes</p>
                                </Jumbotron>)
                            }
                            return (<Jumbotron>
                                <p className="text-center">Como el valor X^2 calculado es mayor a X^2 de la tabla, podemos rechazar que los nmeros son independientes</p>
                            </Jumbotron>)
                        }
                    })()}
                </div>
            </div>

        </div>
    );
};

export default PruebaPoker;
